/**
 * 图片的垂直居中，注意容器不能设置float，会破坏display的效果，有需要建议在外层增加一层div包裹
 *
 * 正常使用，将该方法设置给img的父元素即可:
 *    div.pic-wrapper
 *      img
 * 若元素需要float:
 *    div.float
 *      div.pic-wrapper
 *        img
 * @param width
 * @param height
 */
/**
 * 兼容一个参数的写法，长宽相等
 */
.component-loading {
  padding: 100px 50px;
  text-align: center;
  display: inline-block;
  width: 100%;
  box-sizing: border-box;
}
.component-loading .item-icon {
  display: inline-block;
}
.component-loading .wrong-msg {
  display: inline-block;
  margin-left: 10px;
}
